@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap);
#main{
  font-family: 'Poppins', sans-serif;
  position: relative;
  box-sizing: border-box;
  margin: 0 auto;
  width: 95%;
}
.introduce{
  position: relative;
  font-size: 1rem;
  padding: 10px 0;
  width: 100%;
}
.introduce__title{
  font-size: 2.5em;
  text-align: center;
  font-weight: 600;
  margin: 0.3em 0;
}
.introduce__copy{
  font-size: 1em;
  font-weight: 300;
  text-align: center;
}
.from{
  font-size: 1rem;
  display: flex;
  justify-content: center;
}
.from__input{
  font-size: 1em;
  width: 80%;
}
.group{
  display: flex;
  align-items: flex-start;
  justify-content: left;
  flex-direction: column;
}
.cards{
  padding-left: 0;
  display: grid;
  grid-template-columns: repeat(3, minmax(30%, 1fr));
  width: 100%;
  text-align: center;
  grid-gap: 0.6rem;
  gap: 0.6rem;
}
.card{
  list-style: none;
  display: flex;
  flex-direction: column;
  grid-gap: 0.2rem;
  gap: 0.2rem;
  width: auto;
  height: auto;
}
.card__img{
  width: 100%;
}
